*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  object-fit: scale-down;
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
.button {
  display: block;
  padding: 11px 44px;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #ffffff;
  background-color: #68b738;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  appearance: none;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:focus {
  outline: 2px solid #444444;
  outline-offset: 2px;
}
.button:focus:not(:focus-visible) {
  outline: transparent;
}
.button:focus-visible {
  outline: 2px solid #444444;
  outline-offset: 2px;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--show-all {
  padding: 10px 94px;
  background-color: #f2f2f2;
  color: #444444;
  width: 100%;
}
.button--show-all:hover {
  background-color: #ebebeb;
}
.button--show-all:active {
  color: rgba(68, 68, 68, 0.3);
  background-color: #ebebeb;
}
.button--submit-form {
  padding: 10px 44px;
}
.special-title {
  position: relative;
  padding: 0 20px 0 0;
  display: inline-block;
  width: 100%;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  line-height: 26px;
}
.special-title::before {
  position: absolute;
  top: 15px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #68b738;
  content: "";
}
.special-title__heading {
  position: relative;
  display: inline-block;
  padding: 0 20px 0 20px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .special-title {
    padding-left: 30px;
    font-size: 36px;
    line-height: 36px;
  }
  .special-title::before {
    position: absolute;
    top: 22px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
    content: "";
  }
  .special-title__heading {
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .special-title {
    padding: 0;
  }
  .special-title::before {
    top: 28px;
  }
  .special-title__wrapper {
    display: block;
    max-width: 1300px;
    margin: 0 auto;
  }
}
.text-input {
  position: relative;
  display: flex;
  gap: 13px;
  align-items: center;
  width: 100%;
  margin-bottom: 12px;
}
.text-input:last-child {
  margin-bottom: 0;
}
.text-input__input {
  flex-grow: 1;
  padding: 14px 50px 13px 15px;
  width: 167px;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
  background-color: #ffffff;
  border: 1px solid #e7e7e7;
  appearance: none;
}
.text-input__input--name:invalid,
.text-input__input--name:invalid:focus-within,
.text-input__input--weight:invalid,
.text-input__input--weight:invalid:focus-within,
.text-input__input--email:invalid,
.text-input__input--email:invalid:focus-within,
.text-input__input--tel:invalid,
.text-input__input--tel:invalid:focus-within {
  outline: 2px solid #ff8282;
  outline-offset: -2px;
}
.text-input__input--email:invalid + .text-input__icon,
.text-input__input--email:invalid:focus-within + .text-input__icon,
.text-input__input--tel:invalid + .text-input__icon,
.text-input__input--tel:invalid:focus-within + .text-input__icon {
  color: #ff8282;
}
.text-input__input:hover {
  border: 1px solid #cdcdcd;
}
.text-input__input:focus {
  outline: 2px solid #444444;
  outline-offset: -2px;
}
.text-input__input:focus + .text-input__icon {
  color: #68b738;
}
.text-input__icon {
  position: absolute;
  top: calc(50% - 10px);
  right: 12px;
  color: #000000;
}
.text-input__input::-webkit-outer-spin-button,
.text-input__input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}
.text-input__input--weight,
.text-input__input--age {
  appearance: textfield;
}
@media (min-width: 768px) {
  .text-input {
    margin-bottom: 18px;
  }
  .text-input__icon {
    right: 14px;
  }
  .text-input__input {
    padding: 11px 60px 11px 20px;
    font-size: 20px;
    line-height: normal;
  }
}
@media (min-width: 1440px) {
  .text-input__icon {
    right: 17px;
  }
}
.radio {
  margin-bottom: 11px;
}
.radio:hover .radio__label::before {
  border: 1px solid #bababa;
}
.radio:last-child {
  margin-bottom: 0;
}
.radio__label {
  position: relative;
  display: block;
  padding: 6px 0 0 45px;
  min-height: 32px;
  cursor: pointer;
}
.radio__label::before {
  position: absolute;
  top: calc(50% - 15px);
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
  content: "";
}
.radio__label::after {
  position: absolute;
  top: calc(50% - 8px);
  left: 8px;
  width: 15px;
  height: 15px;
  background-color: #68b738;
  border-radius: 50%;
  content: "";
  display: none;
}
.radio__input:checked + .radio__label::after {
  display: block;
}
.radio__input:focus + .radio__label::before {
  outline: 2px solid #444444;
  outline-offset: 2px;
}
.radio__input:focus:not(:focus-visible) + .radio__label::before {
  outline: transparent;
}
.radio__input:focus-visible + .radio__label::before {
  outline: 2px solid #444444;
  outline-offset: 2px;
}
.radio__input:disabled + .radio__label {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .radio {
    margin-bottom: 12px;
  }
  .radio__label {
    min-height: 40px;
    padding: 5px 0 0 60px;
  }
  .radio__label::before {
    top: calc(50% - 20px);
    width: 40px;
    height: 40px;
  }
  .radio__label::after {
    width: 18px;
    height: 18px;
    top: calc(50% - 9px);
    left: 11px;
  }
}
@media (min-width: 1440px) {
  .radio {
    margin-bottom: 13px;
  }
}
.checkbox:not(:last-child) {
  margin-bottom: 10px;
}
.checkbox:not(:last-child):hover .checkbox__label::before {
  border: 1px solid #bababa;
}
.checkbox__label {
  position: relative;
  display: block;
  padding: 6px 0 0 45px;
  min-height: 32px;
  cursor: pointer;
}
.checkbox__label::before {
  position: absolute;
  top: calc(50% - 15px);
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  content: "";
}
.checkbox__label::after {
  position: absolute;
  top: calc(50% - 16px);
  left: 0;
  width: 30px;
  height: 30px;
  content: "";
  background: url("../img/checkbox-tick.svg") no-repeat center;
  display: none;
}
.checkbox__input:checked + .checkbox__label::after {
  display: block;
}
.checkbox__input:focus + .checkbox__label::before {
  outline: 2px solid #444444;
  outline-offset: 2px;
}
.checkbox__input:focus:not(:focus-visible) + .checkbox__label::before {
  outline: transparent;
}
.checkbox__input:focus-visible + .checkbox__label::before {
  outline: 2px solid #444444;
  outline-offset: 2px;
}
.checkbox__input:disabled + .checkbox__label {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .checkbox:not(:last-child) {
    margin-bottom: 0;
  }
  .checkbox__label {
    min-height: 40px;
    padding: 9px 0 0 60px;
  }
  .checkbox__label::before {
    width: 40px;
    height: 40px;
  }
  .checkbox__label::after {
    width: 26px;
    height: 26px;
    top: calc(50% - 8px);
    left: 7px;
  }
}
