  width: 280px;
  height: 256px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.slider__item {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 280px;
  height: 256px;
  background-color: #eaeaea;
}
.slider__item--before {
  width: 50%;
  transition: width 1s;
  overflow: hidden;
  z-index: 10;
}
.slider__image {
  max-width: none;
  width: 280px;
  height: 256px;
}
