  position: absolute;
  left: calc(50% - 2px);
  top: -2px;
  width: 4px;
  height: 100%;
  padding: 0;
  border: none;
  border-radius: 2px;
  background-color: #666666;
  appearance: none;
  cursor: pointer;
  z-index: 20;
  transition: left 1s;
}
.slider__button::before {
  content: "";
  position: absolute;
  top: calc(50% - 9px);
  left: calc(50% - 12px);
  width: 24px;
  height: 18px;
  background: #ffffff;
  mask-image: url("../img/stack.svg#slider-thumb");
  background-color: #666666;
  mask-repeat: no-repeat;
  z-index: 10;
}
.slider__button::after {
  content: "";
  position: absolute;
  top: calc(50% - 20px);
  left: calc(50% - 20px);
  width: 40px;
  height: 40px;
  background-color: #ffffff;
  border: 2px solid #666666;
  border-radius: 50%;
}
.slider__button:hover {
  background-color: #68b738;
}
.slider__button:hover::before {
  background-color: #68b738;
}
.slider__button:hover::after {
  border-color: #68b738;
}
.slider__button:focus {
  outline: transparent;
  background-color: #68b738;
}
.slider__button:focus::before {
  outline: 3px solid #68b738;
  outline-offset: 3px;
  background-color: #68b738;
}
.slider__button:focus::after {
  border-color: #68b738;
}
.slider__button:focus:not(:focus-visible)::before {
  outline: transparent;
}
.slider__button:focus-visible::before {
  outline: 3px solid #68b738;
  outline-offset: 3px;
}
.slider__button:active {
  background-color: #5eaa2f;
}
.slider__button:active::before {
  background-color: #5eaa2f;
}
.slider__button:active::after {
  border-color: #5eaa2f;
}
.slider--state-before .slider__item--before {
  width: 100%;
}
.slider--state-after .slider__item--before {
  width: 0;
}
.slider--state-before .slider__button {
  left: 100%;
}
.slider--state-after .slider__button {
  left: 0;
}
