.checkbox:not(:last-child) {
  margin-bottom: 10px;

  &:hover .checkbox__label::before {
    border: 1px solid #bababa;
  }
}

.checkbox__label {
  position: relative;
  display: block;
  padding: 6px 0 0 45px;
  min-height: 32px;
  cursor: pointer;
}

.checkbox__label::before {
  position: absolute;
  top: calc(50% - 15px);
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  content: "";
}

.checkbox__label::after {
  position: absolute;
  top: calc(50% - 16px);
  left: 0;
  width: 30px;
  height: 30px;
  content: "";
  background: url("../img/checkbox-tick.svg") no-repeat center;
  display: none;
}

.checkbox__input:checked + .checkbox__label::after {
  display: block;
}

.checkbox__input:focus + .checkbox__label::before {
  outline: 2px solid #444444;
  outline-offset: 2px;
}

.checkbox__input:focus:not(:focus-visible) + .checkbox__label::before {
  outline: transparent;
}

.checkbox__input:focus-visible + .checkbox__label::before {
  outline: 2px solid #444444;
  outline-offset: 2px;
}

.checkbox__input:disabled + .checkbox__label {
  opacity: 0.3;
}

@media (min-width: @tablet-width) {
  .checkbox:not(:last-child) {
    margin-bottom: 0;
  }

  .checkbox__label {
    min-height: 40px;
    padding: 9px 0 0 60px;
  }

  .checkbox__label::before {
    width: 40px;
    height: 40px;
  }

  .checkbox__label::after {
    width: 26px;
    height: 26px;
    top: calc(50% - 8px);
    left: 7px;
  }
}
